<template>
  <div class="container">
    <h1>测试测试</h1>
    <el-form label-position="top" label-width="80px" :model="form" :rules="rules" ref="ruleForm">
      <el-form-item label="姓名" prop="name">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="身份证" prop="idCard">
        <el-input v-model="form.idCard"></el-input>
      </el-form-item>
      <el-form-item label="点下方选颜色" prop="">
        <el-color-picker v-model="form.timeColor"></el-color-picker>
      </el-form-item>
    </el-form>
    <el-button type="primary" class="btn" @click="submitForm('ruleForm')">生成</el-button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form: {
        name: '',
        idCard: '',
        timeColor: '#409EFF'
      },
      rules: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' }
        ],
        idCard: [
          { required: true, message: '请输入身份证号', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.form.name = localStorage.getItem('name')
    this.form.idCard = localStorage.getItem('idCard')
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.create()
        } else {
          return false
        }
      })
    },
    create () {
      // if (!this.form.time) {
      //   this.form.time = dayjs().format('YYYY-MM-DD')
      // }

      // this.$axios.$post('https://b9088.xyz:3000/api/user', this.form)
      localStorage.setItem('name', this.form.name)
      localStorage.setItem('idCard', this.form.idCard)
      this.$router.push({
        path: '/home',
        query: this.form
      })
    }
  }
}
</script>

<style scoped>
.container {
  width: 80%;
  margin: 50px auto;
}
h1 {
  text-align: center;
  font-size: 24px;
